import React, {useState, useEffect} from 'react';

function App() {

  //CLEAN UP USEEFFECT EXAMPLE

  // const [windowWidth, setWindowWidth] = useState(() => {
  //   return window.innerWidth;
  // })

  // const handleResize = () => {
  //   setWindowWidth(window.innerWidth);
  // }

  // useEffect(() => {
  //   window.addEventListener('resize', handleResize);

  //   //runs everytime the useEffect changes in order to clean out your
  //   //previous code - in this case, remove the event listener
  //   return () => {
  //     window.removeEventListener('resize', handleResize);
  //   }
  // }, [])


  // return (
  //     <div>{windowWidth}</div>
  // );


  const [resourceType, setResourceType] = useState(() => { return "posts" })
  const [items, setItems] = useState(() => { return [] })

  useEffect (() => {
    fetch(`https://jsonplaceholder.typicode.com/${resourceType}`)
    .then(response => response.json())
    // .then(json => console.log(json))
    .then(json => setItems(json))
  }, [resourceType])

  return (
    <>
      <div>
        <button onClick={() => setResourceType("posts")}>Posts</button>
        <button onClick={() => setResourceType("users")}>Users</button>
        <button onClick={() => setResourceType("comments")}>Comments</button>
      </div>
      <h1>{resourceType}</h1>
      {items.map(item => {
        return <pre>{JSON.stringify(item)}</pre>
      })}
    </>
  );
}

export default App;
